/* Feature */
.feature-item {
    flex: 1;
    padding: 2.5rem;
}
    
.feature-icon {
    border: 10px solid #00bc77;
    border-radius: 50%;
    padding: 1rem;
    width: 100px;
}
  
.feature-item-title {
    color: #222;
    font-size: 1.25rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
}