/* Header */
.header {
    color: #fff;
    margin-bottom: 2rem;
}

/* Button */
#edit-button {
    background-color: #00bc77;
    border-color: #00bc77;
    color: #fff;
    font-weight: bold;
    padding: 10px;
}
  
.save-button {
    background-color: #00bc77;
    border-color: #00bc77;
    color: #fff;
    font-weight: bold;
    margin-right: 10px;
    min-width: 200px;
    padding: 10px;
}

.cancel-button {
    background-color: #fff;
    border-color: #00bc77;
    color: #00bc77;
    font-weight: bold;
    margin-left: 10px;
    min-width: 200px;
    padding: 10px;
}

/* Form */
#edit-section {
    display: none;
}

/* Input */ 
.profil-input-wrapper {
    display: inline-flex;
    flex-direction: column;
    margin-bottom: 1rem;
    text-align: left;
}
  
.profil-input-wrapper input {
    font-size: 1.2rem;
    margin: 10px 5px;
    padding: 5px;
}
